@import './variables.scss';

.loader {
    border: 16px solid $secondary-color;
    /* Light grey */
    border-top: 16px solid $third-color;
    /* Blue */
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
 
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}